.spinner:after, .spinner:before {
  content: "";
  box-sizing: border-box;
  border: 4px dashed #fff;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: 4s linear infinite tape;
  display: block;
  position: absolute;
  bottom: -4px;
  transform: rotate(0);
}

.spinner:before {
  right: 0;
  box-shadow: 0 0 0 4px #fff, 0 0 0 34px #000;
}

.spinner:after {
  left: 0;
  box-shadow: 0 0 0 4px #fff, 0 0 0 65px #000;
}

@keyframes tape {
  0% {
    transform: rotate(0)scale(.4);
  }

  100% {
    transform: rotate(-360deg)scale(.4);
  }
}

